@import "theme";

.nav-link {
    padding-right: 16px !important;
}

.navbar-brand {
    font-size: 1.75rem;
    color: $brand-color !important;
    margin-left: 10px;
    font-weight: 700;
    font-family: 'Barlow', 'Poppins', sans-serif;
}

#sidebarToggler {
    width: $size-38;
    height: $size-50;
    background: $nav-background;
    padding-top: 0;
    padding-left: 10px;
    padding-right: 0;
    border: none;
    outline: none;
    display: none;

    &:focus {
        outline: 0;
    }

    span {
        width: 80%;
        height: 3px;
        display: block;
        background: $sidebar-toggle-line-color;
        transition: all 0.5s cubic-bezier(0.810, -0.330, 0.345, 1.375);
        transition-delay: 0.2s;
        transform: none;
        opacity: 1;
        margin: 5px auto;
    }

    &.active span {
        &:first-of-type {
            // rotate the first one
            transform: rotate(45deg) translate(4px, 8px);
        }

        &:nth-of-type(2){
            // rotate the second one
            opacity: 0;
        }

        &:last-of-type{
            // rotate the third one
            transform: rotate(-45deg) translate(3px, -7px);
        }  
    }       
}

#sidebar {
    background: $nav-background;
    color: $nav-text-color;
    min-width: $nav-side-width;
    max-width: $nav-side-width;
    position: fixed;
    z-index: 999;
    height: 100vh;
    top: $size-68;
    
    .sidebar-header {
        padding: $size-20;
        background: $nav-background-highlight;
    }

    ul {

        li {
            
            a{
                color: inherit;
                padding: 10px;
                font-size: 1em;
                display: block;
                text-decoration: none;
            }

            &:hover, &.active > a {
                color: $nav-text-highlight;
                background-color: $nav-background-highlight;
            }

        }

        
    }
}

.overlay {
    // display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: -1;
    opacity: 0;
    transition: all 0.3s ease-in-out;

    &.active {
        // display: block;
        opacity: 1;
        z-index: 998;
    }
}


#dismiss {
    width: 35px;
    height: 35px;
    position: absolute;
    top: 5px;
    right: 15px;
    text-align: right;
    display: none;
}




/* --------------------------------------
            MAIN CONTENT 
 --------------------------------------*/
 #content {
    margin-top: $size-68;
    min-height: 100vh;
    padding: $size-20;
    margin-left: $nav-side-width;
    transition: all 0.3s;

    &.no-side {
        margin-left: 0;
    }
}


// 屏幕小的时候(小于 992)
@media (max-width: 992px) {
    #sidebarToggler {
        display: inline-block;
    }

    #sidebar {
        // top: 52px;
        margin-left: -$nav-side-width;
        transition: all 0.5s;

        &.active {
            margin-left: 0;
            transform: none;
        }
    }

    #dismiss {
        display: inline-block;
    }

    #content {
        margin-left: 0;
    }
}
