//google font
@import "theme";

$theme-body-bg: white;

html, body {
    font-family: 'Barlow','Playfair Display SC', Arial, serif;
    /* height: 100%; */
    min-width: 750px;
    margin: 0;
    background-color: $theme-body-bg;
}

* {
    // border: 1px solid red; 
    box-sizing: border-box;
}

.container {
    width: 90%;
    /* height: 100%; */
    margin: 12px auto;
    min-width: 750px;
    background-color: $theme-body-bg;
}

.page-header {
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: start;
    overflow: hidden;
    position: relative;
    z-index: 1;
    font-family: 'Catamaran';
    padding-top: 6px;
    padding-left: 12px;

    color: rgb(0,0,0);
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);

    div a {
        color: rgb(0,0,0);
    }

    .info-row {
        width: 40%;
        display: flex;
        align-items: start;
        justify-content: space-between;
    }

    .icon {
        position: absolute;
        font-size: 1.4rem;
        width:100%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-around !important;
        padding-right: 20px;

        i {
            color: white;
            margin-right: 6px;
            margin-left: 6px;
        }
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.7;
        width: 100%;
        z-index: -1;
        margin: 0;
    }

    > * {
        margin: 4px;
    }
}

.page-content {   
    /* direct child 都是 flex item */
    display: flex;

    /* flex-direction: row; */
    justify-content: space-around;
    align-items: top;
    flex-wrap: nowrap;   
    font-size: 0.9 rem;

    > div:first-child {
        flex-grow: 1;
        flex-shrink: 1;
        min-width: 250px;
    }

    > div:nth-child(2) {
        flex-grow: 1;
        flex-shrink: 1;
        min-width: 500px;
    }

    a {
        color: black;
    }

    .content-title {
        color: white;
        background-color: rgba(68,131,68,0.9);
        display: inline;
        padding: 0 5px 0 5px;
    }
}

/* side */
.side-content-item{
    margin: 5px 0 25px;

    > * {
        margin: 6px 0;
    }

    * {
        margin: 2px 0;
    }

    i {
        font-size: 1.5rem;
        margin-right: 15px;
    }

    ul {
        list-style: none;
        padding-left: 0;
    }
}

.side-content {
    padding-top: 0.8rem;
    padding-left: 0.7rem;
    padding-right: 0.7rem;
    background-color: #f8de7e60;
    
}

/* main */
.main-content-item{
    margin: 5px 0 25px;

    > * {
        margin: 6px;
    }

    * {
        margin: 2px 0;
    }

    ul {
        list-style: none;
        padding-left: 10px;
        margin: 3px;
    }
}

.main-content {
    padding-top: 0.8rem;
    padding-left: 0.7rem;
    padding-right: 0.7rem;

}

/*****************/

.content-title {
    font-size: 1.3rem;
    letter-spacing: 0.1rem;
    margin: 6px 0;
}

.content-sub-title{
    font-weight: 600;
    margin:0;
}

.bullet {
    margin-right: 5px;
    font-size: 1.2 rem;
}

.experience-item-title {
    display: flex; 
    justify-content: space-between; 
    width: 100%; 
    align-items: center;
    margin: 0;
}

.job-title {
    margin: 0;
}

.section {
    margin: 0;
}
